import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';
import { Sales } from "src/app/Model/Sales";
import { MotorType } from 'src/app/Model/MotorType';
import { VehicleModel } from 'src/app/Model/VehicleModel';
import { BaseResponse } from 'src/app/Model/base-response';


@Injectable({
  providedIn: 'root'
})
export class SaleService extends BaseApiHandlerService {
  salesObserver$:Observable<Sales[]>;
  private saleSubject$: BehaviorSubject<Sales[]> = new BehaviorSubject<Sales[]>(undefined);

  motortypeObserver$: Observable<MotorType[]>;
  private motortypeSubject$: BehaviorSubject<MotorType[]> = new BehaviorSubject<MotorType[]>(undefined);


  vehicleModelObserver$: Observable<VehicleModel[]>;
  private vehicleModelSubject$: BehaviorSubject<VehicleModel[]> = new BehaviorSubject<VehicleModel[]>(undefined);

  public get Sales(){ return this.saleSubject$.value}

  constructor(private http:HttpClient) {
    super(http);
    this.salesObserver$= this.saleSubject$.asObservable();
    this.motortypeObserver$=this.motortypeSubject$.asObservable();
    this.vehicleModelObserver$=this.vehicleModelSubject$.asObservable();
   }
   SaveSale(data){
    return this.Post(data,API_URL+API_ENDPOINTS.Sales);
  }
  UpdateSale(data:Sales){
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Sales);
  }
  GetSale(page=1,pageSize=10){
    return this.GetAll(API_URL+API_ENDPOINTS.Sales+`/GetPaginated?page=${page}&pageSize=${pageSize}`).subscribe(res =>{
      this.saleSubject$.next(res.dynamicResult);
    });
  }
  GetTotalSales()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Sales+`/GetTotalSales`);
  }
  SearchSales(search)
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Sales+`/Search?search=${search}`).subscribe(res=>{
      this.saleSubject$.next(res.dynamicResult);
    });
  }
  GetMotorType()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.MotorType).subscribe(res=>{
      this.motortypeSubject$.next(res.dynamicResult);
    })
  }
  GetVehicleModel()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.VehicleModel).subscribe(res=>{
      this.vehicleModelSubject$.next(res.dynamicResult);
    })
  }
  DeleteSale(id){
     return this.Delete(id,API_URL+API_ENDPOINTS.Sales);
   }
   filterByDateRangeAndSearchTerm(fromDate, toDate, searchTerm: string,requestExcel:boolean): Observable<BaseResponse> {
    if(searchTerm){
      return this.GetAll(API_URL+API_ENDPOINTS.SalesFilter+`?fromDate=${fromDate}&toDate=${toDate}&requestExcel=${requestExcel}`);
    }
    else{
      return this.GetAll(API_URL+API_ENDPOINTS.SalesFilter+`?fromDate=${fromDate}&toDate=${toDate}&searchTerm=${searchTerm==null?"":searchTerm}&requestExcel=${requestExcel}`);
    }
   }
   Filter(from:string,to:string){
    return this.GetAll(API_URL+API_ENDPOINTS.SalesFilter+`?from=${from}&to=${to}`);
   }
}
