import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Company } from 'src/app/Model/Company';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { StatementRequestDTO } from 'src/app/Model/StatemenReqDTO';
import { DownloadWithData, MasterStatement, Statement } from 'src/app/Model/Statement';
import { BaseApiHandlerService } from '../base-api-handler.service';

@Injectable({
  providedIn: 'root'
})
export class CompanyService extends BaseApiHandlerService {

  companyObserver$:Observable<Company[]>;
  statementObserver$:Observable<DownloadWithData>;
  private companySubject$: BehaviorSubject<Company[]> = new BehaviorSubject<Company[]>(undefined);
  private statementSubject$: BehaviorSubject<DownloadWithData> = new BehaviorSubject<DownloadWithData>(undefined);

  public get companies() {return this.companySubject$.value;}
  constructor(private http:HttpClient)
  {
    super(http)
    this.companyObserver$=this.companySubject$.asObservable();
    this.statementObserver$=this.statementSubject$.asObservable();
  }

  // GetBrokerStatement(companyId)
  // {
  //   console.log('chal rha')
  //   return this.GetAll(API_URL+API_ENDPOINTS.Transaction+`/GetBrokerStatement?companyId=${companyId}`).subscribe(res=>{
  //     this.statementSubject$.next(res.dynamicResult);
  //   });
  // }

  GetBrokerStatement(req:StatementRequestDTO)
  {
    console.log('chal rha')
    return this.Post(req,API_URL+API_ENDPOINTS.Transaction+`/GetBrokerStatement`).subscribe(res=>{
      this.statementSubject$.next(res.dynamicResult);
    });
  }
  GetAgentStatement(req:StatementRequestDTO)
  {
    console.log('chal rha')
    return this.Post(req,API_URL+API_ENDPOINTS.Transaction+`/GetAgentStatement`).subscribe(res=>{
      this.statementSubject$.next(res.dynamicResult);
    });
  }
  GetBankStatement(req:StatementRequestDTO)
  {
    return this.Post(req,API_URL+API_ENDPOINTS.Transaction+`/GetBankStatement`).subscribe(res=>{
      this.statementSubject$.next(res.dynamicResult);
    });
  }

  SaveCompanies(data)
  {
    return this.Post(data,API_URL+API_ENDPOINTS.Company);
  }
  UpdateCompany(data:Company)
  {
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Company)
  }
  GetCompanies()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Company).subscribe(res=>{
      this.companySubject$.next(res.dynamicResult);
    });
  }

  DeleteCompany(id)
  {
    return this.Delete(id,API_URL+API_ENDPOINTS.Company);
  }

}
