import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Broker } from 'src/app/Model/broker';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service'; 

@Injectable({
  providedIn: 'root'
})
export class BrokerService extends BaseApiHandlerService  {
  brokerObserver$:Observable<Broker[]>;
  private brokerSubject$: BehaviorSubject<Broker[]> = new BehaviorSubject<Broker[]>(undefined);

  public get brokers() {return this.brokerSubject$.value;}

  constructor(private http:HttpClient) 
  {
    super(http)
    this.brokerObserver$=this.brokerSubject$.asObservable();
  }Savebrokers(data)
  {
    return this.Post(data,API_URL+API_ENDPOINTS.Broker);
  }
  Updatebroker(data:Broker)
  {
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.Broker)
  }
  Getbrokers()
  {
    return this.GetAll(API_URL+API_ENDPOINTS.Broker).subscribe(res=>{
      this.brokerSubject$.next(res.dynamicResult);
    });
  }

  Deletebroker(id)
  {
    return this.Delete(id,API_URL+API_ENDPOINTS.Broker);
  }

}